@import url(http://fonts.googleapis.com/earlyaccess/jejugothic.css);

@font-face {
  font-family: 'JejuGothic';
  src: local('JejuGothic'), url(/school-startingday-calculator/static/media/JejuGothic.67e5ae84.ttf) format('ttf');
}

body {

   background: #e0e5ec;

}
body{--color:0, 0%;--l:100%;--color-primary-lightest: hsl(var(--color),var(--l));--color-primary-light: hsl(var(--color),calc(var(--l) - 7.5%));--color-primary: hsl(var(--color),calc(var(--l) - 10%));--color-primary-dark: hsl(var(--color),calc(var(--l) - 15%));--color-primary-darkest: hsl(var(--color),calc(var(--l) - 25%));display:flex;justify-content:center;align-items:center;background-color:var(--color-primary-dark)}body:before,body:after{-webkit-filter:blur(5px);filter:blur(5px);content:'';position:absolute;width:100%;height:100%;background-size:cover;top:0;left:0;opacity:1;z-index:1}body:after{background-size:cover}body .wrapper{margin:60px auto;font-family:'Jeju Gothic', serif}body .neumorphism{background-color:#e0e5ec;box-shadow:9px 9px 16px rgba(163,177,198,0.6),-9px -9px 16px rgba(255,255,255,0.5)}body .card-w .card{display:flex;flex-direction:column;align-items:center;padding:60px 30px;border-radius:30px;position:relative;background:transparent;border-radius:20px;border:1px solid rgba(255,255,255,0.2)}body .card-w .card .card__text{text-align:center}body .card-m .card{display:flex;flex-direction:column;align-items:center;padding:40px 30px;border-radius:30px;position:relative;background:transparent;border-radius:20px;border:1px solid rgba(255,255,255,0.2)}body .card-m .card .card__text{text-align:center}body .card.inset{box-shadow:inset 3px 3px 7px rgba(136,165,191,0.48),inset -3px -3px 7px #fff}body .card.inset i{text-shadow:1px 1px 1px #d6e1ef99, 0 0 0 #000, 1px 1px 1px #d6e1ef00}body .card.active{box-shadow:9px 9px 16px rgba(163,177,198,0.6),-9px -9px 16px rgba(255,255,255,0.5),inset 3px 3px 7px rgba(136,165,191,0.48),inset -3px -3px 7px #fff;background:linear-gradient(318.32deg, rgba(163,177,198,0.1) 0%, rgba(163,177,198,0.1) 55%, rgba(163,177,198,0.25) 100%)}body textarea{position:absolute;width:100vw;height:50vh;top:calc(50% - 25vh);opacity:0;z-index:999}body textarea::selection{background:transparent}body textarea:focus ~ h1 span:not(.text){opacity:1}body h1{margin:0;font-family:'Jeju Gothic', serif;font-size:8rem;color:#2B2B29;text-shadow:-5px -3px 8px var(--color-primary-dark),6px 2px 12px var(--color-primary-darkest);outline:none;text-transform:uppercase;max-width:800px;width:auto;margin:0;position:relative;line-height:1.2;pointer-events:none;word-break:break-word}body h1 span{display:inline}body h1 span:nth-of-type(2){opacity:0;transition:opacity 0.2s ease-in-out;position:relative;font-size:6.5rem;display:inline-block;margin-left:1rem;-webkit-animation:flash 0.5s ease-in-out infinite alternate;animation:flash 0.5s ease-in-out infinite alternate;-webkit-transform:translateY(-1.25rem);transform:translateY(-1.25rem)}body h1 span:nth-of-type(2):before,body h1 span:nth-of-type(2):after{opacity:0}@-webkit-keyframes flash{to{text-shadow:0px 0px 8px var(--color-primary),0px 0px 12px var(--color-primary-darkest);color:var(--color-primary-dark)}}@keyframes flash{to{text-shadow:0px 0px 8px var(--color-primary),0px 0px 12px var(--color-primary-darkest);color:var(--color-primary-dark)}}body h1 span:before,body h1 span:after{content:attr(data-text);position:absolute;display:inline;color:inherit;z-index:9;text-shadow:-5px -3px 3px var(--color-primary-light),6px 2px 5px var(--color-primary-darkest);-webkit-filter:blur(2.5px);filter:blur(2.5px);opacity:200;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);left:0;width:100%;height:100%}body h1 span:after{-webkit-text-stroke:3px var(--color-primary-darkest);text-shadow:none;-webkit-mask:none;mask:none;opacity:0.02}body .segment{padding:32px 0;text-align:center;font-family:'Jeju Gothic', serif}body .innertext{color:#2B2B29;text-shadow:3px 3px 3px #fff}body .infotext{text-align:center;color:#2B2B29;text-shadow:1px 1px 1px #fff;font-size:105%;font-family:'Jeju Gothic', serif;justify-content:center}body .infonumtext{text-align:center;color:#57648E;text-shadow:1px 1px 1px #fff;font-size:105%;font-family:'Jeju Gothic', serif;justify-content:center}body .destext{text-align:center;color:grey;text-shadow:1px 1px 1px #fff;font-size:50%;font-family:'Jeju Gothic', serif;justify-content:center}

