/* variables */
$bkg: #f7f8fc;
$ruler: 16px;
$l-grad-hex-100: #efeff6;
$color-shadow: #BABECC;
$color-white: #FFF;
$l-grad-rgba-100: rgba(179, 202, 226, 0.28);
@import url(http://fonts.googleapis.com/earlyaccess/jejugothic.css);
$d-grad-100: dark;
$noise: url('data:image/png;base64,');


body{
     --color:0, 0%;
  --l:100%; 
  --color-primary-lightest: hsl(var(--color),var(--l));
  --color-primary-light: hsl(var(--color),calc(var(--l) - 7.5%));
   --color-primary: hsl(var(--color),calc(var(--l) - 10%));
   --color-primary-dark: hsl(var(--color),calc(var(--l) - 15%));
  --color-primary-darkest: hsl(var(--color),calc(var(--l) - 25%)); 
    display:flex;
   // height:100vh;
    //width:100vw;
    justify-content:center;
    align-items:center;
    background-color: var(--color-primary-dark);
  &:before, &:after{
    filter:blur(5px);
    content:'';
    position:absolute;
    width:100%;
    height:100%;
    //background:linear-gradient(-45deg, var(--color-primary-darkest) 5%, transparent 50%) no-repeat center center fixed;
    //mask:$noise;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    top:0;
    left:0;
    opacity:1;
    z-index:1;
  }
  &:after{
    //background:linear-gradient(135deg, var(--color-primary-light) 5%, transparent 50%);
    background-size: cover;
  }
.wrapper{
  margin: 60px auto;
  font-family: 'Jeju Gothic', serif;
}

.neumorphism {
  background-color: #e0e5ec;
  //dark
  box-shadow: 9px 9px 16px rgba(163, 177, 198, 0.6), -9px -9px 16px rgba(255, 255, 255, 0.5);
  //light
  //box-shadow: 4px 2px 16px rgba(136, 165, 191, 0.48), -4px -2px 16px #FFFFFF;
}

.card-w{
  
  .card{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 30px;
    border-radius: 30px;
    position: relative;
  background: transparent;
  border-radius: 20px;
  border: 1px solid rgba(255,255,255,0.2);

    .card__icon{
      
    }
    .card__text{
      text-align: center;
    }
  }
}

.card-m{
  
  .card{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 30px;
    border-radius: 30px;
    position: relative;
  background: transparent;
  border-radius: 20px;
  border: 1px solid rgba(255,255,255,0.2);

    .card__icon{
      
    }
    .card__text{
      text-align: center;
    }
  }
}



/* inset */
.card.inset {
    box-shadow: inset 3px 3px 7px rgba(136, 165, 191, 0.48), inset -3px -3px 7px #FFFFFF;
    i {
        text-shadow: 1px 1px 1px #d6e1ef99, 0 0 0 #000, 1px 1px 1px #d6e1ef00;
    }
}

.card.active {
    box-shadow: 9px 9px 16px rgba(163, 177, 198, 0.6),
      -9px -9px 16px rgba(255, 255, 255, 0.5),
       inset 3px 3px 7px rgba(136, 165, 191, 0.48), 
      inset -3px -3px 7px #FFFFFF;
    background: linear-gradient(318.32deg, rgba(163, 177, 198, 0.1) 0%, rgba(163, 177, 198, 0.1) 55%, rgba(163, 177, 198, 0.25) 100%);
}

textarea{
  position:absolute;
  width:100vw;
  height:50vh;
  top:calc(50% - 25vh);
  opacity:0;
  z-index:999;
  &::selection{
    background:transparent;
  }
  &:focus{
    & ~ h1 span:not(.text){
      opacity:1;
    }
  }
}
h1{
  
  margin:0;
  font-family:'Jeju Gothic', serif;
  font-size:8rem;
  color: #2B2B29;
  text-shadow:-5px -3px 8px var(--color-primary-dark),
  6px 2px 12px var(--color-primary-darkest);
  outline:none;
  text-transform:uppercase;
  max-width:800px;
  width:auto;
  margin:0;
  position:relative;
  line-height:1.2;
  pointer-events:none;
  word-break:break-word;
  span{
   display:inline;
    &:nth-of-type(2){
      opacity:0;
      transition:opacity 0.2s ease-in-out;
      position:relative;
      font-size:6.5rem;
      display:inline-block;
      margin-left:1rem;
      animation:flash 0.5s ease-in-out infinite alternate;
     transform:translateY(-1.25rem);
      &:before, &:after{
        opacity:0;
      }
      @keyframes flash{
        to{
          text-shadow:0px 0px 8px var(--color-primary),
  0px 0px 12px var(--color-primary-darkest);
          color:var(--color-primary-dark);
        }
      }
    }
    &:before, &:after{
      content:attr(data-text);
      position:absolute;
      display:inline;
      color:inherit;
      z-index:9;
      text-shadow:-5px -3px 3px var(--color-primary-light),
    6px 2px 5px var(--color-primary-darkest);
      filter:blur(2.5px);
      opacity:200;
      top:50%;
      transform:translateY(-50%);
      left:0;
      width:100%;
      height:100%;
    }
    &:after{
     -webkit-text-stroke:3px var(--color-primary-darkest);
      text-shadow:none;
      mask:none;
      opacity:0.02;
    }
  }
}
.segment {
  padding: $ruler*2 0;
  text-align: center;
  font-family: 'Jeju Gothic', serif;
}

.innertext {
  color: #2B2B29;
